import React from 'react';

import Flatpickr from 'react-flatpickr'
import 'flatpickr/dist/themes/material_green.css'
import '../styles/AddProductModal.css';

const AddProductModal = ({ onAddProductSubmit, onAddProductCancel }) => {

  function onFormSubmit(ev) {
    ev.preventDefault();
    const data = {
      name: ev.target.elements.name.value,
      category: ev.target.elements.category.value,
      description: ev.target.elements.description.value,
      price: ev.target.elements.price.value,
      picUrl: "https://bulma.io/images/placeholders/1280x960.png",
      dispDate: document.getElementsByClassName('flatpickr-input')[0].value
    }
    console.log(data);
    onAddProductSubmit(data);
  }

  return (
    <div className={ 'AddProductModal modal is-active' }>

      <div className="modal-background"></div>
      <form className="modal-card" onSubmit={ onFormSubmit }>

        <header className="modal-card-head">
          <p className="modal-card-title">Add Item</p>
          <button className="delete" type="button" aria-label="close" onClick={ onAddProductCancel }></button>
        </header>

        <section className="modal-card-body">

          <div className="field">
            <label className="label">Name</label>
            <div className="control">
              <input className="input" name="name" type="text"/>
            </div>
          </div>

          <div className="field">
            <label className="label">Category</label>
            <div className="control">
              <div className="select">
                <select name="category">
                  <option>Meuble</option>
                  <option>Objet</option>
                </select>
              </div>
            </div>
          </div>

          <div className="field">
            <label className="label">Description</label>
            <div className="control">
              <textarea className="textarea" name="description" type="text"/>
            </div>
          </div>

          <div className="field">
            <label className="label">Price</label>
            <div className="control">
              <input className="input" name="price" type="number"/>
            </div>
          </div>

          <div className="field">
            <label className="label">Disponibility Date</label>
            <div className="control">
              <Flatpickr 
                className="input"
                value={new Date()}
                onChange={date => { console.log(date) }} />
            </div>
          </div>

          <label className="label">Image</label>
          <div className="file has-name">
            <label className="file-label">
              <input className="file-input" type="file" name="resume"/>
              <span className="file-cta">
                <span className="file-icon">
                  <i className="fas fa-upload"></i>
                </span>
                <span className="file-label">
                  Choose a file…
                </span>
              </span>
              <span className="file-name">
                Screen Shot 2017-07-29 at 15.54.25.png
              </span>
            </label>
          </div>
        </section>

        <footer className="modal-card-foot">

          <div className="field is-grouped">
            <p className="control">
              <button className="button is-primary" type="submit">
                Confirm
              </button>
            </p>
            <p className="control">
              <button className="button is-light" type="button" onClick={ onAddProductCancel }>
                Cancel
              </button>
            </p>
          </div>

        </footer>
        
      </form>
    </div>
  )
};

export default AddProductModal;



